<template>
  <div class="edit">
    <div class="form-group">
      <input id="title" type="text" class="form-control" v-model="title" placeholder="title" disabled>
      <textarea class="form-control" rows="15" v-model="content" placeholder="content"></textarea>
      <br>
      <button v-on:click="sendMemo" class="btn btn-outline-dark"> Update </button>
    </div>

  </div>
</template>

<script>
import axios from 'axios'

const serverAdress = 'http://localhost:9000'
const memoListUri = serverAdress + '/memo'

export default {
  name: 'editpage',
  props: ['ptitle', 'pcontent'],
  data: function () {
    return {
      title: this.ptitle,
      content: this.pcontent
    }
  },
  methods: {
    sendMemo: function () {
      axios
        .put(memoListUri + '/' + this.title, {
          'title': this.title,
          'content': this.content
        }).then(p => {
          this.$root.$emit('editEnd')
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.edit {
  margin-left: auto;
  margin-right: auto;
}

@media all and (min-width: 700px) {
  .edit {
    width : 700px
  }
}
</style>



