<template>
  <div class="page">
    <p>
      <router-link to="/NewMemo"> <button class="btn btn-outline-primary"> New </button> </router-link>
    <p>
    <ul>
      <li v-for="memo in memoList" :key="memo">
        <button v-on:click="getMemo(memo)" class="btn btn-outline-info"> {{memo}} </button>
      </li>
    </ul>

    <div v-if="path.length && editMode == false">
      <hr />
      <h1> {{title}} </h1>
      <hr />
      <pre> {{content}} </pre>
      <hr />
      <p>
        <button v-on:click="editMode = true" class="btn btn-outline-dark"> Edit </button>
        <button v-on:click="deleteMemo(path)" class="btn btn-outline-dark"> Delete </button>
      </p>
    </div>

    <div v-if="editMode == true">
      <EditMemo :ptitle="title" :pcontent="content"> </EditMemo>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import EditMemo from '@/components/EditMemo'

const serverAdress = 'http://localhost:9000'
const memoListUri = serverAdress + '/memo'

export default {
  name: 'MemoList',
  components: {
    EditMemo: EditMemo
  },
  data () {
    return {
      path: '',
      title: '',
      content: '',
      editMode: false,
      memoList: []
    }
  },
  mounted () {
    this.getMemoList()
  },
  created () {
    this.$root.$on('editEnd', () => {
      this.editMode = false
      this.initList()
    })
  },
  methods: {
    getMemoList: function (event) {
      axios
        .get(memoListUri)
        .then(response => {
          this.memoList = response.data.memos
        })
    },
    getMemo: function (param) {
      this.path = param
      axios
        .get(memoListUri + '/' + this.path)
        .then(res => {
          this.title = res.data.title
          this.content = res.data.content
          this.editMode = false
        })
    },
    initList () {
      // 내용을 지워준다.
      this.title = ''
      this.content = ''
      this.path = ''
      this.editMode = false
      this.getMemoList()
    },
    deleteMemo: function (param) {
      axios
        .delete(memoListUri + '/' + param)
        .then(res => {
          this.initList()
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

pre {
  text-align: left;
  padding: 10px;
}

.page {
  border-bottom: 1px solid lightgray;
  border-left: 1px solid lightgray;
  border-right: 1px solid lightgray;
  margin-left: auto;
  margin-right: auto;
}

.btn {
  margin-bottom: 10px;
}

@media all and (min-width: 700px) {
  .page {
    width : 700px
  }
}
</style>



