import Vue from 'vue'
import Router from 'vue-router'
import MemoList from '@/components/MemoList'
import NewMemo from '@/components/NewMemo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MemoList',
      component: MemoList
    },
    {
      path: '/NewMemo',
      name: 'NewMemo',
      component: NewMemo
    }
  ]
})



