<template>
  <div class="edit">
    <h2>
      New memo
    </h2>
    <div class="form-group">
      <input id="title" type="text" class="form-control" v-model="title" placeholder="title">
      <textarea class="form-control" rows="15" v-model="content" placeholder="content"></textarea>
      <br>

      <router-link to="/"> <button class="btn btn-outline-dark"> Back </button> </router-link>
      <button v-on:click="sendMemo" class="btn btn-outline-dark"> Submit </button>
    </div>

  </div>
</template>

<script>
import axios from 'axios'

const serverAdress = 'http://localhost:9000'
const memoListUri = serverAdress + '/memo'

export default {
  name: 'editpage',
  data () {
    return {
      title: '',
      content: ''
    }
  },
  methods: {
    sendMemo: function () {
      axios
        .post(memoListUri + '/' + this.title, {
          'title': this.title,
          'content': this.content
        }).then((post) => {
          this.$router.push('/')
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.edit {
  margin-left: auto;
  margin-right: auto;
}

@media all and (min-width: 700px) {
  .edit {
    width : 700px
  }
}
</style>



